"use client"
import { useState, useEffect } from "react";
import { useRouter } from "next/router"; // Імпорт useRouter для редиректу
import Sidebar from "@/components/Sidebar";
import Provider from "./provider";
import { createClientComponentClient } from "@supabase/auth-helpers-nextjs"; // Клієнтський Supabase
import type { Viewport } from "next";
import "./globals.css";

// Конфігурація viewport
export const viewport: Viewport = {
  width: "device-width",
  initialScale: 1,
  themeColor: "#FFFFFF",
  colorScheme: "light",
};

// Metadata сторінки
export const metadata = {
  title: "SocialHub",
  description: "Automation",
};

export default function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  const [isLoading, setIsLoading] = useState(true); // Додаємо стан для відображення лоадера
  const [isAuthenticated, setIsAuthenticated] = useState<boolean | null>(null); // Стан автентифікації
  const router = useRouter(); // Для виконання редиректу

  useEffect(() => {
    const checkAuth = async () => {
      const supabase = createClientComponentClient(); // Ініціалізація клієнта Supabase
      const { data: { session }, error } = await supabase.auth.getSession();

      if (error || !session) {
        setIsAuthenticated(false); // Якщо немає сесії або є помилка, авторизація не пройдена
        router.replace("/auth"); // Перенаправлення на сторінку авторизації
      } else {
        setIsAuthenticated(true); // Якщо є сесія, користувач авторизований
      }
      setIsLoading(false); // Завершення завантаження
    };

    checkAuth();
  }, [router]);

  // Якщо сторінка ще завантажується, відображаємо лоадер
  if (isLoading) {
    return <div>Loading...</div>;
  }

  return (
    <html lang="en">
      <body>
        <Provider>
          {isAuthenticated ? (
            <>
              <Sidebar />
              <main>{children}</main>
            </>
          ) : (
            <div>Unauthorized</div>
          )}
        </Provider>
      </body>
    </html>
  );
}
